.nav{
    position: fixed;
    top: 0;
    width: 100%;
    display: flex;
    /* justify-content: space-between; */
    padding: 20px;
    height: 27px;
    z-index: 1;
    transition-timing-function: ease-in;
    transition: all 0.5s;

}

.nav_black{
    background-color: #111;
    
}

.nav_logo {
    width: 80px;
    object-fit: contain;
    position: fixed;
    left:20px;

}

.nav_avatar{
    width: 30px;
    object-fit: contain;
    position: fixed;
    right:20px;
}